<template>
  <div class="pay">
    <div class="pay-top">
      <van-nav-bar title="订单" left-text="返回" left-arrow @click-left="onClickLeft" />
    </div>
    <div class="pay-body">
      <div class="paymap">
        <div class="mapbox" is-link @click="showPopup">
          <div class="mapbox-text">
            <strong>{{currentAddress.address}}</strong>
            <p>{{currentAddress.name}}-{{currentAddress.tel}}</p>
          </div>
          <van-icon name="arrow" class="mapbox-textjt" />
        </div>
        <div>
          <van-popup v-model="show" round position="bottom" :style="{ height: '500px' }">
            <van-address-list
              v-model="chosenAddressId"
              :list="addresslist"
              @click-item="clickitem"
              add-button-text="新增地址"
              @add="gonewAddres"
              default-tag-text="默认"
              @edit="editaddress"
            />
          </van-popup>
        </div>

        <div class="timebox">
          <strong>立即送出</strong>
          <div class="timebox-text">
            <p>
              大约
              <span>13:50</span>
              送达
            </p>
            <van-icon name="arrow" class="timebox-jt" />
          </div>
        </div>
      </div>
      <div class="shopbagitembox">
        <div class="shopbagitembox-Text">订单详情</div>
        <div class="shopbagitem" v-for="(item,index) in payData" :key="index">
          <van-swipe-cell>
            <van-cell :border="false">
              <div class="cellitem">
                <div class="imgbox">
                  <img :src="item.small_img" alt />
                </div>
                <div class="smbox">
                  <div class="textbox">
                    <div class="namebox">
                      <p>
                        <strong>{{item.name}}</strong>
                        <span>{{item.rule}}</span>
                      </p>
                    </div>
                    <div class="pricebox">
                      <span>￥{{item.price}}</span>
                      <strong>X{{item.count}}</strong>
                    </div>
                  </div>
                </div>
              </div>
            </van-cell>
          </van-swipe-cell>
        </div>
      </div>
    </div>
    <div class="pay-foot clearfix">
      <div class="payfoot-text">
        <div class="allnumbox">
          <span>￥{{product.allprice}}</span>
        </div>
        <div class="allpricebox">
          <span>共{{product.allnum}}件商品</span>
        </div>
      </div>
      <div class="btnbox" @click="pay">立即结算</div>
    </div>
    <div class="bg"></div>
  </div>
</template>

<script>
import "../assets/less/pay.less";
import { utils } from "../assets/js/utils";
export default {
  created() {
    // 获取购物车传过来的sid
    this.sids = this.$route.query.sids.split("-");

    this.getPayData();
    //获取地址数据
    this.getaddresslist();
  },
  data() {
    return {
      sids: [],
      isedit: false,
      checked: false,
      show: false,
      chosenAddressId: "",
      currentAddress: {
        name: "",
        tel: "",
        address: "",
      },
      // 结算商品数据
      payData: [],
      // 地址数据
      addresslist: [],
      // 计数和价格
      product: {
        allnum: 0,
        allprice: 0,
      },
    };
  },
  methods: {
    //返回上一级
    onClickLeft() {
      this.$router.go(-1);
    },
    //显示弹出层
    showPopup() {
      this.show = true;
    },
    //跳转到新增地址界面
    gonewAddres() {
      this.$router.push({ name: "Newaddress" });
    },
    //获取结算的商品数据
    getPayData() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push("/login");
      }
      this.axios({
        method: "GET",
        url: "/commitShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(this.sids),
        },
      })
        .then((result) => {
          if (result.data.code == 50000) {
            result.data.result.map((v) => {
              this.product.allnum += v.count;
              this.product.allprice += v.count * v.price;
            });
          }
          this.product.allprice = this.product.allprice + ".00";

          this.payData = result.data.result;
          if (result.data.result.length == 0) {
            this.$router.go(-1);
          }
        })
        .catch((err) => {});
    },
    //获取地址数据
    getaddresslist() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 20000) {
            let allbool = [];
            result.data.result.map((v) => {
              let address = v.province + v.city + v.county + v.addressDetail;
              this.addresslist.push({
                id: v.aid,
                name: v.name,
                tel: v.tel,
                address,
                isDefault: Boolean(v.isDefault),
              });
              if (Boolean(v.isDefault)) {
                this.chosenAddressId = v.aid;
                this.currentAddress.name = v.name;
                this.currentAddress.tel = v.tel;
                this.currentAddress.address = address;
              }
              allbool.push(v.isDefault);
            });
            allbool = allbool.every((v) => {
              // 都为0即无默认值
              return v == 0;
            });
            if (allbool) {
              this.currentAddress.name = "请设置默认地址";
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    // 点击地址
    clickitem(item, index) {
      this.chosenAddressId = item.id;
      this.currentAddress.name = item.name;
      this.currentAddress.tel = item.tel;
      this.currentAddress.address = item.address;
      this.show = false;
    },
    //立即结算
    pay() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push("/login");
      }
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        sids: JSON.stringify(this.sids),
        phone: this.currentAddress.tel,
        receiver: this.currentAddress.name,
        address: this.currentAddress.address,
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "POST",
        url: "/pay",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          //
          if (result.data.code == 60000) {
            setTimeout(() => {
              this.$router.push("/order");
            }, 700);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    // 跳转home界面
    gohome() {
      this.$router.push("/main");
    },
    // 编辑地址
    editaddress(item, index) {
      this.$router.push({
        name: "Newaddress",
        query: { aid: item.id },
      });
    },
  },
};
</script>
